<template>
  <section class="card-group row row-cols-3 text-center" ref="productInfo">
    <div class="card col">
      <div class="card-body">
        <a href="#" class="text-uppercase text-decoration-none" @click.prevent="category = 'pie'">
          pie<CircleOne :class="{ 'd-none': category !== 'pie' }" />
        </a>
      </div>
    </div>
    <div class="card col w-left">
      <div class="card-body">
        <a href="#" class="text-uppercase text-decoration-none" @click.prevent="category = 'cake'">
          cake<CircleTwo :class="{ 'd-none': category !== 'cake' }" />
        </a>
      </div>
    </div>
    <div class="card col w-left">
      <div class="card-body">
        <a
          href="#"
          class="text-uppercase text-decoration-none"
          @click.prevent="category = 'cookie'"
        >
          cookie<CircleThree :class="{ 'd-none': category !== 'cookie' }" />
        </a>
      </div>
    </div>
  </section>
  <section class="card-group row">
    <div class="card p-4">
      <ul class="row row-cols-2 row-cols-sm-4 g-3">
        <template v-for="(item, index) in productsList" :key="index">
          <li v-if="item.category === category" class="col">
            <a
              href="#"
              class="text-decoration-none position-relative"
              @click.prevent="getProduct(item)"
            >
              {{ item.title }}
              <CircleOne :class="{ 'd-none': item.id !== tempProduct.id }" />
            </a>
          </li>
        </template>
      </ul>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 7rem;
}
.circle-2 {
  width: 8rem;
}
.circle-3 {
  width: 10rem;
}
</style>

<script>
import CircleOne from '@/components/models/circle/CircleOne.vue';
import CircleTwo from '@/components/models/circle/CircleTwo.vue';
import CircleThree from '@/components/models/circle/CircleThree.vue';
import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      category: 'pie',
      productsList: [],
      tempProductList: [],
      tempProduct: {},
    };
  },
  components: {
    CircleOne,
    CircleTwo,
    CircleThree,
  },
  props: ['products'],
  watch: {
    products() {
      this.productsList = this.products;
      this.setTempProducts();
    },
    category() {
      this.setTempProducts();
    },
    tempProduct() {
      emitter.emit('infoData', this.tempProduct);
    },
  },
  methods: {
    setTempProducts() {
      this.tempProductList = [];
      this.productsList.forEach((item) => {
        if (this.category === item.category) {
          this.tempProductList.push(item);
        }
      });
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.tempProductList[0];
    },
    getProduct(data) {
      // 觸發上層scrollTo滑動到上方
      emitter.emit('scrollTo');
      this.tempProduct = data;
    },
  },
};
</script>
