<!-- eslint-disable max-len -->
<template>
  <Loading :isLoading="isLoading" />
  <!-- 上半部 -->
  <section class="card-group row w-tab" ref="productInfo">
    <!-- 詳細商品 -->
    <div class="card col-sm-6 p-0">
      <ProductInformation />
    </div>
    <!-- 購物流程 -->
    <div class="card col-sm-6">
      <ShopProcess @addCart="addToCart" :carts="carts" />
    </div>
  </section>
  <!-- 下半部 -->
  <!-- 產品列表 -->
  <ProductList :products="products" />
</template>

<script>
// components
import ProductInformation from '@/components/shop/ProductInformation.vue';
import ShopProcess from '@/components/shop/ShopProcess.vue';
import ProductList from '@/components/shop/ProductList.vue';
import Loading from '@/components/models/LoadingModel.vue';

import emitter from '@/assets/javascript/emitter';

export default {
  data() {
    return {
      isLoading: false,
      products: [],
      carts: [],
      category: 'pie',
      categoryList: [],
      tempProduct: {},
    };
  },
  components: {
    ShopProcess,
    ProductInformation,
    ProductList,
    Loading,
  },
  watch: {
    carts() {
      emitter.emit('cartData', this.carts);
    },
  },
  methods: {
    getProducts() {
      this.isLoading = true;
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http.get(api).then((res) => {
        this.products = res.data.products;
        this.isLoading = false;
      });
    },
    getCarts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data;
      });
    },
    addToCart(data) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(api, { data: { product_id: this.tempProduct.id, qty: 1, data } })
        .then((res) => {
          console.log(res);
          this.getCarts();
        });
    },
    scrollToProductInfo() {
      this.$refs.productInfo.scrollIntoView({ behavior: 'smooth' });
    },
  },
  mounted() {
    if (this.$route.query.category) {
      this.category = this.$route.query.category;
    }
    this.getProducts();
    this.getCarts();
    emitter.on('scrollTo', () => {
      this.scrollToProductInfo();
    });
  },
};
</script>
